import React, { Component } from 'react';
import { Row, Col, Form, Input } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import { BtnSearch, BtnClear } from 'components/common/button';
import { getQueryParams, updateQueryParams } from 'utils/urlUtil';
import { getGroupsForSearchAssociationMem } from 'services/groupsForSearchAssociationMem';
import { SelectAjax } from 'components/common';
import { requiredRule } from 'utils/validateRules';

@Form.create({ name: 'FormSearchStuForAssoMem' })
class FormSearchStuForAssoMem extends Component {
  state = {};

  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      updateQueryParams(values);
    });
  };

  handleReset = () => {
    this.props.form.resetFields();
    updateQueryParams();
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate
            form={this.props.form}
            optionFieldDecorator={{
              trainTypeId: {
                rules: [requiredRule],
              },
              facultyId: {
                rules: [requiredRule],
              },
              courseId: {
                rules: [requiredRule],
              },
              studyFieldId: {
                rules: [requiredRule],
              },
            }}
          />
          <Col span={6}>
            <Form.Item label="Hội">
              {getFieldDecorator('groupId', {})(
                <SelectAjax service={getGroupsForSearchAssociationMem} />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Mã SV">{getFieldDecorator('studentCode', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Họ tên">
              {getFieldDecorator('studentFullName', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormSearchStuForAssoMem;



