import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, Button } from 'antd';
import { TableData, CartHeadAction } from 'components/common';
import { BtnExport } from 'components/common/button';
import { Link } from 'react-router-dom';
import {
  getStudentsForAssociationMem,
  deletePoliticalDate,
  exportPoliticStudents,
} from 'services/studentForAssociationMem';
import { getQueryParams } from 'utils/urlUtil';
import { dateToString } from 'utils/formatUtil';
import routeConstant from 'routes/route/routeConstant';
import FormSearchStuForAssoMem from './FormSearchStuForAssoMem';
import './style.scss';

class StuAssociationMemPage extends Component {
  state = {
    reload: 0,
    paramSearch: {},
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  handleKhaiTru = async ({ studentId, groupId }) => {
    try {
      await deletePoliticalDate({ studentId, groupId });
      this.setState({ reload: this.state.reload + 1 });
    } catch (error) {
      console.log(error);
    }
  };

  handleExport = async ({
    trainTypeId,
    facultyId,
    courseId,
    studyFieldId,
    classId,
    groupId,
    studentCode,
    studentFullName,
  }) => {
    try {
      await exportPoliticStudents({
        trainTypeId,
        facultyId,
        courseId,
        studyFieldId,
        classId,
        groupId,
        studentCode,
        studentFullName,
      });
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    const columns = [
      {
        title: 'Mã SV',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'Họ tên',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Ngày sinh',
        dataIndex: 'dob',
        key: 'dob',
      },
      {
        title: 'Giới tính',
        dataIndex: 'genderText',
        key: 'genderText',
      },
      {
        title: 'Tên lớp',
        dataIndex: 'className',
        key: 'className',
      },
      {
        title: 'Tên hội',
        dataIndex: 'groupName',
        key: 'groupName',
      },
      {
        title: 'Ngày kết nạp',
        dataIndex: 'politicalDate',
        render: (text, record) => {
          if (record.politicalDate == null) {
            return <Fragment />;
          }
          return <Fragment>{dateToString(record.politicalDate, 'DD/MM/YYYY')}</Fragment>;
        },
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => {
          if (record.politicalDate) {
            return (
              <Fragment>
                <Button
                  className="btnEventStuPartyMem btnKhaiTru"
                  type="primary"
                  onClick={() =>
                    this.handleKhaiTru({ studentId: record.id, groupId: record.groupId })
                  }
                >
                  Khai trừ
                </Button>
              </Fragment>
            );
          }
          return (
            <Link
              to={{
                pathname: routeConstant.DOAN_HOI__HOI_VIEN__NHAP_DU_LIEU,
                search: `?id=${record.id}`,
              }}
            >
              <Button className="btnEventStuPartyMem btnKetNap" type="primary">
                Kết nạp
              </Button>
            </Link>
          );
        },
      },
    ];
    const listParamSearch = this.state.paramSearch;
    return (
      <Card title="Đảng viên là sinh viên">
        <CartHeadAction>
          <BtnExport
            title="Xuất Excel"
            onClick={() =>
              this.handleExport({
                trainTypeId: listParamSearch.trainTypeId,
                facultyId: listParamSearch.facultyId,
                courseId: listParamSearch.courseId,
                studyFieldId: listParamSearch.studyFieldId,
                classId: listParamSearch.classId,
                groupId: listParamSearch.groupId,
                studentCode: listParamSearch.studentCode,
                studentFullName: listParamSearch.studentFullName,
              })
            }
          />
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <FormSearchStuForAssoMem />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              paramSearch={this.state.paramSearch}
              service={getStudentsForAssociationMem}
              allowGetData={
                !!listParamSearch.trainTypeId &&
                !!listParamSearch.facultyId &&
                !!listParamSearch.courseId &&
                !!listParamSearch.studyFieldId
              }
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <StuAssociationMemPage />
  </Page>
);



